<template>
    <div id="view-todo">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Todo number: {{todo_id}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteTodo" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-todo', params: {todo_id: todo_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'view-todo',
    data () {
        return {
            todo_id: null,
            name: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('todos').where('todo_id',
        '==', to.params.todo_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.todo_id = doc.data().todo_id
                    vm.name = doc.data().name
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('todos').where('todo_id', '==', this.$route.params.todo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.todo_id = doc.data()
                    .todo_id
                    this.name = doc.data().name
                })
            })
        },
        deleteTodo () {
            if(confirm('Are you sure?')) {
                 db.collection('todos').where('todo_id', '==', this.$route.params.todo_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>


