<template>
    <div id="edit-todo">
        <h3>Edit Todo</h3>
        <div class="row">
            <form @submit.prevent="updateTodo" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="todo_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-todo',
    data () {
        return {
            todo_id: null,
            name: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('todos').where('todo_id',
        '==', to.params.todo_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.todo_id = doc.data().todo_id
                    vm.name = doc.data().name
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('todos').where('todo_id', '==', this.$route.params.todo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.todo_id = doc.data()
                    .todo_id
                    this.name = doc.data().name
                })
            })
        },
        updateTodo () {
            db.collection('todos').where('todo_id', '==', this.$route.params.todo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        todo_id: this.todo_id,
                        name: this.name
                    })
                    .then(() => {
                        this.$route.push({name: 'view-todo', params: {todo_id: this.todo_id}})
                    })
                })
            })
        }
    }
}
</script>


